    export default {
        name: 'user-admin',
	    data() {
            return {
                lineHeight: '68rpx',
            	getFocus: false,
                active: 0,
	            search: true,
	            remarkTextarea: false,
	            look: false,
	            error: false,
                keyword: '',
                status: '',
                page: 1,
	            over: false,
	            list: [],
	            memberLevel: null,
	            storeList: [],
                rechargeBool: false,
                placeholder: '积分',
                rechargeStatus: 0,
                num: '',
                remark_name: '',
                remark: '',
	            rechargeItem: {},
                rechargeDeduction: 0, // 0 扣除 1 充值
                modifyStoreBool: false,
	            storeItem: {},
                storeIndex: -1,
                storeUser: {},
	            sort: 0,
	            dialog: false,
	            dialogChoose: true,
	            changeRemark: false,
	            changeMember: false,
                releaseBool: false,
                member: [],
                deleteObject: {}
            }
	    },
	    onLoad() { this.$commonLoad.onload();
            let screenWidth = uni.getSystemInfoSync().windowWidth;
            let p = 375 / screenWidth;
            this.lineHeight = 68*p + 'rpx';
            this.$request({
	            url: this.$api.app_admin.user,
	            data: {
                    page: this.page,
		            status: this.status,
		            keyword: this.keyword
	            }
            }).then(response => {
                if (response.code === 0) {
                    this.list = response.data.list;
                	for(let item of this.list) {
                		if(item.remark) {
                			item.remarkLength = this.strlen(item.remark)
                		}
                	}
                    this.member = response.data.mall_members;
                    let para = {
                    	level: 0,
                    	id: 0,
                    	name: response.data.general_user_text ? response.data.general_user_text : '普通用户',
                    }
                    this.member.unshift(para);
                }
            });
	    },
        onReachBottom() {
            if (!this.over) {
                this.page++;
                this.threeRequest(this.active);
            }
        },
	    methods: {
	    	toLook(item) {
	    		this.remarkTextarea = true;
	    		this.look = true;
	    		this.remark = item.remark
	    	},
	    	strlen(str){
			    var len = 0;
			    for (var i=0; i<str.length; i++) {
			     var c = str.charCodeAt(i);
			    //单字节加1
			     if ((c >= 0x0001 && c <= 0x007e) || (0xff60<=c && c<=0xff9f)) {
			       len++;
			     }
			     else {
			      len+=2;
			     }
			    }
			    return len;
			},
            async request({url, data}) {
                const response = await this.$request({
	                url: url,
	                data: data
                });
                if (response.code === 0) {
                    if (response.data.list.length > 0) {
                        return response.data;
                    } else {
                        this.over = true;
                        return false;
                    }
                }
            },
            clearSearch() {
                this.keyword = '';
                this.keywordSearch();
            },
            chooseMember(level) {
            	this.memberLevel = level;
            },
            toChangeInfo(data) {
            	this.dialog = !this.dialog;
            	this.changeRemark = false;
	            this.remarkTextarea = false;
            	this.dialogChoose = true;
            	this.changeMember = false;
            	this.remark_name = null;
            	if(data) {
                	this.rechargeItem = data;
            	}else {
            		this.rechargeItem = {};
            	}
            },
            toChangeRemark(num) {
            	this.dialogChoose = false;
            	if(num == 1) {
	            	this.changeRemark = true;
	            	this.remark_name = this.rechargeItem.remark_name;
            	}else {
	            	this.remarkTextarea = true;
	            	this.remark = this.rechargeItem.remark;
            	}
            },
            toChangeMember() {
            	this.dialogChoose = false;
            	if(this.member.length > 0) {
	            	this.changeMember = true;
	            	this.memberLevel = this.rechargeItem.member_level
            	}else {
            		this.error = true;
            	}
            },
            close() {
            	this.error = false;
            	this.dialog = false;
            },
            toLevel() {
                this.$request({
                    url: this.$api.app_admin.level,
                    method: 'post',
                    data: {
                    	id: this.rechargeItem.user_id,
                        member_level: this.memberLevel,
                    },
                }).then(response => {
                    if (response.code === 0) {
                    	this.toChangeInfo();
                    	this.setActive(0);
                    }else {
                    	uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                });
            },
            toRemark() {
                this.$request({
                    url: this.$api.app_admin.remark,
                    method: 'post',
                    data: {
                    	id: this.rechargeItem.user_id,
                        remark: this.remark
                    },
                }).then(response => {
                    if (response.code === 0) {
                    	this.rechargeItem.remark = this.remark;
                    	this.rechargeItem.remarkLength = this.strlen(this.rechargeItem.remark)
                    	this.toChangeInfo();
                    }else {
                    	uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                });
            },
            toRemarkName() {
                this.$request({
                    url: this.$api.app_admin.remarkName,
                    method: 'post',
                    data: {
                    	id: this.rechargeItem.user_id,
                        remark_name: this.remark_name
                    },
                }).then(response => {
                    if (response.code === 0) {
                    	this.rechargeItem.remark_name = this.remark_name;
                    	this.toChangeInfo();
                    }else {
                    	uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                });
            },
            setSearch() {
                this.search = false;
            },
            setActive(data) {
                this.list = [];
                this.page = 1;
                this.over = false;
                this.keyword = '';
                this.sort = 0;
                uni.showLoading({
                    title: '加载中...'
                });
                this.threeRequest(data);
                this.active = data;
                this.search = true;
            },
		    threeRequest(data) {
	            uni.showLoading({
	                mask: true,
	                title: '加载中...'
	            });
                if (data === 0) {
                    this.request({
                        url: this.$api.app_admin.user,
                        data: {
                            page: this.page,
                            status: '',
                            keyword: this.keyword,
                        }
                    }).then(response => {
                		uni.hideLoading();
                        if (response === false) return;
                    	this.list = [...this.list, ...response.list];
                    	for(let item of this.list) {
                    		if(item.remark) {
                    			item.remarkLength = this.strlen(item.remark)
                    		}
                    	}
                    });
                } else if (data === 1) {
                    this.request({
                        url: this.$api.app_admin.share,
                        data: {
                            page: this.page,
                            status: 1,
                            keyword: this.keyword,
                            sort: this.sort,
                        }
                    }).then(response => {
                    	uni.hideLoading();
                        if (response === false) return;
                        this.list = [...this.list, ...response.list];
                    });
                } else if (data === 2) {
                    this.request({
                        url: this.$api.app_admin.clerk,
                        data: {
                            page: this.page,
                            status: '',
                            keyword: this.keyword,
	                        sort: this.sort,
                        }
                    }).then(response => {
                    	uni.hideLoading();
                        if (response === false) return;
                        this.list = [...this.list, ...response.list];
                        this.storeList = response.store_list;
                    });
                }
		    },
            inputBlur() {
                let that = this;
                setTimeout(v =>{
	            	that.getFocus = false;
	                if (that.keyword === '') that.search = true;
                },300);
            },
            setRechargeDeduction(data, status) {
                this.rechargeItem = data;
                this.rechargeBool = true;
                this.rechargeDeduction = status;
            },
            activeRecharge(data) {
            	let that = this;
                that.rechargeStatus = data;
                that.num = '';
                if (data === 0) {
                    that.placeholder = '积分';
                } else {
                    that.placeholder = '余额';
                }
            },
            checkRecharge(data) {
            	let that = this;
            	if(that.rechargeStatus === 1) {
            		let num = (+data.detail.value).toFixed(2)
            		setTimeout(()=>{
            			that.num = Number(num)
            		})
            	}
            },
		    clearStatus() {
                // 默认积分
                this.rechargeStatus = 0;
                // 充值对象
                this.rechargeItem = {};
                // 充值框
                this.rechargeBool = false;
                // 充值扣除
                this.rechargeDeduction = 0;
                // 充值量
                this.num = '';
                this.remark = '';
		    },
            cancelRecharge() {
                this.rechargeBool = false;
                this.clearStatus();
            },
		    intergral(num, type) {
                this.$request({
                    url: this.$api.app_admin.integral, // 积分
                    method: 'post',
                    data: {
                        user_id: this.rechargeItem.user_id,
                        num: num,
                        remark: this.remark ? this.remark : '',
                        pic_url: '',
                        type: type
                    }
                }).then(response => {
                    if (response.code === 0) {
                        if (this.rechargeDeduction === 1) {
                            this.setList('integral', num,1);
                        } else {
                            this.setList('integral', num, 0);
                        }
                        this.clearStatus();
                    } else if (response.code === 1) {
                        uni.showToast({
                            title: response.msg,
                            image: '../image/mark.png'
                        });
                    }
                })
		    },
            balance(num, type) {
                this.$request({
                    url: this.$api.app_admin.balance, // 余额
                    method: 'post',
                    data: {
                        user_id: this.rechargeItem.user_id,
                        price: num,
                        remark: this.remark ? this.remark : '',
                        pic_url: '',
                        type: type
                    }
                }).then(response => {
                    if (response.code === 0) {
                        if (this.rechargeDeduction === 1) {
                            this.setList('balance', num,1);
                        } else {
                            this.setList('balance', num, 0);
                        }
                        this.clearStatus();
                    } else if (response.code === 1) {
                        uni.showToast({
                            title: response.msg,
                            image: '../image/mark.png'
                        });
                    }
                })
            },
		    setList(key, num, algorithm) {
                this.list.map(item => {
                    if (item.user_id === this.rechargeItem.user_id) {
                        if (algorithm === 0) {
                            item[key] = Number(item[key]) - num;
                        } else {
                            item[key] = Number(item[key]) + num;
                        }
                    }
                });
		    },
            confirmRecharge() {
                this.$utils.debounce(this.sureCharge, 500, true);
            },
            sureCharge() {
                if (!isNaN(this.num)) {
                    this.rechargeBool = false;
                    if (this.rechargeDeduction === 1) {
                        if (this.rechargeStatus === 0) {
                            this.intergral(Number(this.num), 1);
                        } else if (this.rechargeStatus === 1) {
                            this.balance(Number(this.num), 1);
                        }
                    } else if (this.rechargeDeduction === 0) {
                        if (this.rechargeStatus === 0) {
                            this.intergral(Number(this.num), 0);
                        } else if (this.rechargeStatus === 1) {
                            this.balance(Number(this.num), 0);
                        }
                    }
                }
            },
            modifyStore(data) {
                this.storeUser = data;
                this.storeItem = data.store[0];
                for (let i = 0; i < this.storeList.length; i++) {
                    if (this.storeItem.id === this.storeList[i].id) {
                        this.storeIndex = i;
                    }
                }
                this.modifyStoreBool = true;
            },
            pickerChange(data) {
                this.storeIndex = data.detail.value[0];
            },
            dismissal(data) {
                this.deleteObject = data;
                this.releaseBool = true;
            },
            determineStore() {
                this.$request({
	                url: this.$api.app_admin.clerk_edit,
	                method: 'post',
	                data: {
						user_id: this.storeUser.user_id,
		                store_id: this.storeList[this.storeIndex].id,
		                id: this.storeUser.id,
	                }
                }).then(response => {
                    if (response.code === 0) {
                       this.$nextTick().then(() => {
                           for (let i = 0; i < this.list.length; i++) {
                               if (this.list[i].user.id === this.storeUser.user_id) {
                                   this.list[i].store[0] = this.storeList[this.storeIndex];
                                   this.storeIndex = -1;
                                   this.storeUser = {};
                                   this.modifyStoreBool = false;
                                   this.storeItem = {};
                               }
                           }
                       })
                    }
                })
            },
            allSort(data) {
                this.over = false;
                this.page = 1;
                uni.pageScrollTo({
                    scrollTop: 0
                });

                if (data === 0) {
                	this.sort === 1 ? this.sort = 2 : this.sort = 1;
                } else if (data === 1) {
					this.sort === 3 ? this.sort = 4 : this.sort = 3;
                }

                let sort = undefined;
				switch (this.sort) {
					case 1:
						sort = 'price_count_asc';
						break;
					case 2:
						sort = 'price_count_desc';
						break;
					case 3:
						sort = 'order_count_asc';
						break;
					case 4:
						sort = 'order_count_desc';
						break;
					default:
						sort = '';
				}
                this.$request({
	                url: this.$api.app_admin.user,
	                data: {
	                    page: 1,
                        keyword: this.keyword,
                        status: '',
                        sort: sort
	                }
                }).then(res => {
                    if (res.code === 0) {
                        this.list = res.data.list;
                    }
                })
            },
            setSort(data) {
                this.over = false;
                this.page = 1;
                uni.pageScrollTo({
                    scrollTop: 0
                });
                switch (data) {
					case 0:
						this.sort === 1 ? this.sort = 2 : this.sort = 1;
						break;
					case 1:
						this.sort === 3 ? this.sort = 4 : this.sort = 3;
						break;
					case 2:
						this.sort === 5 ? this.sort = 6 : this.sort = 5;
						break;
				}
                uni.showLoading({
                    title: '加载中...'
                });
                this.$request({
	                url: this.$api.app_admin.clerk,
	                data: {
	                    page: 1,
                        keyword: this.keyword,
                        status: '',
                        sort: this.sort
	                }
                }).then(response => {
                    uni.hideLoading();
                    if (response.code === 0) {
                        this.list = response.data.list;
                    }
                })
            },
            cancelUndelete() {
                this.releaseBool = false;
                this.deleteObject = {};
            },
            sureDeletion() {
                uni.showLoading({
                    title: '加载中...'
                });
                this.$request({
	                url: this.$api.app_admin.clerk_destroy,
	                method: 'post',
	                data: {
	                    id: this.deleteObject.id
	                }
                }).then(response => {
                    uni.hideLoading();
                    if (response.code === 0) {
                        this.list.map((item, index) => {
                            if (item.id === this.deleteObject.id) {
                                this.$delete(this.list, index);
                            }
                        });
                        this.deleteObject = {};
                        this.releaseBool = false;
                    }
                });
            },
		    keywordSearch() {
                this.page = 1;
                this.over = false;
                uni.showLoading({
                    title: '加载中...'
                });
                if (this.active === 0) {
                    this.request({
                        url: this.$api.app_admin.user,
                        data: {
                            page: this.page,
                            status: '',
                            keyword: this.keyword
                        }
                    }).then(response => {
                    	uni.hideLoading();
                        if (response === false) {
                            return this.list = [];
                        }
                        this.list = response.list;
                    	for(let item of this.list) {
                    		if(item.remark) {
                    			item.remarkLength = this.strlen(item.remark)
                    		}
                    	}
                    });
                } else if (this.active === 1) {
                    this.request({
                        url: this.$api.app_admin.share,
                        data: {
                            page: this.page,
                            status: 1,
                            keyword: this.keyword
                        }
                    }).then(response => {
                    	uni.hideLoading();
                        if (response === false) {
                            return this.list = [];
                        }
                        this.list = response.list;
                    });
                } else if (this.active === 2) {
                    this.request({
                        url: this.$api.app_admin.clerk,
                        data: {
                            page: this.page,
                            status: '',
                            keyword: this.keyword,
                            sort: this.sort
                        }
                    }).then(response => {
                    	uni.hideLoading();
                        if (response === false) {
                            return this.list = [];
                        }
                        this.list = response.list;
                    });
                }
		    },
            distributorSort(data) {
                this.over = false;
                this.page = 1;
                uni.pageScrollTo({
                    scrollTop: 0
                });
                if (data === 0) {
					this.sort === 1 ? this.sort = 2 : this.sort = 1;
                } else {
					this.sort === 3 ? this.sort = 4 : this.sort = 3;
                }
                uni.showLoading({
                    title: '加载中...'
                });
                this.$request({
                    url: this.$api.app_admin.share,
                    data: {
                        page: 1,
                        keyword: this.keyword,
                        status: 1,
                        sort: this.sort
                    }
                }).then(response => {
                    uni.hideLoading();
                    if (response.code === 0) {
                        this.list = response.data.list;
                    }
                })
            }
 	    }
    }
